<template>
  <v-container fill-height v-if="!settings">
    <v-layout row wrap align-center>
      <v-flex xs8 offset-xs2>
        <v-layout row wrap text-xs-center>
          <v-flex xs12>
            <h3>Loading</h3>
          </v-flex>
          <v-flex xs12>
            <v-progress-linear v-bind:indeterminate="true"></v-progress-linear>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
  <v-container v-else>
    <v-layout row>
      <v-flex xs12 md10 offset-md1>
        <v-card>
          <v-toolbar color="color3" dark>
            <v-toolbar-title>
              Payment Account
            </v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-layout flex row>
              <v-flex xs12 md6>
                Volleyball Life processes tournament registration payments through Stripe Cloud Payments Platform.
                The payments are processed by our platform, on your behalf, as direct charges to your Stripe account. 
                The full charge amount - less the Stripe fees and  
                <v-tooltip top :nudge-top="15">
                  <a slot="activator">transaction fees</a>
                  <span>Transaction fees may include:<br>Volleyball Life transaction fees<br>Any sanctionaing body fees and<br>   (i.e. the AVP's portion of the entry fee)</span>
                </v-tooltip> - is deposited into your Stripe account.
                In order for us to do process payments for your tournament, you need to connect or create a Stripe account.
              </v-flex>
              <v-flex xs12 md6>
                <v-container fill-height align-center justify-center v-if="settings.stripeAccounts.length == 0">
                  <a href="#" @click="connect" class="stripe-connect"><span>Connect with Stripe</span></a>
                </v-container>
                <v-list v-else dense two-line subheader>
                  <v-subheader>Connected Accounts</v-subheader>
                  <v-list-tile avatar v-for="(account, i) in settings.stripeAccounts" :key="i">
                    <v-list-tile-avatar>
                      <v-icon class="blue white--text">credit_card</v-icon>
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                      <v-list-tile-title>{{ account.displayName }}</v-list-tile-title>
                      <v-list-tile-sub-title>{{ account.businessName }}</v-list-tile-sub-title>
                    </v-list-tile-content>
                  </v-list-tile>
                </v-list>
              </v-flex>
            </v-layout>
            
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex'
import SDK from '../../../VBL'

export default {
  props: ['username'],
  data () {
    return {
      sdk: null,
      settings: null
    }
  },
  computed: {
    ...mapGetters([
      'user',
      'getPageInfo'
    ]),
    userIsAdmin () {
      return this.user && this.user.isPageAdmin(this.username)
    }

  },
  methods: {
    getSettings () {
      this.sdk.organization.getSettings(this.getPageInfo(this.username).id)
        .then((response) => {
          this.settings = response.data
        })
        .catch((error) => {
          console.log(error)
        })
    },
    connect () {
      this.sdk.organization.getConnectURL(this.getPageInfo(this.username).id)
        .then((response) => {
          window.location = response.data
        })
        .catch((error) => {
          console.log(error)
        })
    }
  },
  created () {
    this.sdk = new SDK(this.axios)
    this.getSettings()
  }
}
</script>

<style>
.stripe-connect {
  display: inline-block;
  margin-bottom: 1px;

  background-image: -webkit-linear-gradient(#28A0E5, #015E94);
  background-image: -moz-linear-gradient(#28A0E5, #015E94);
  background-image: -ms-linear-gradient(#28A0E5, #015E94);
  background-image: linear-gradient(#28A0E5, #015E94);

  -webkit-font-smoothing: antialiased;
  border: 0;
  padding: 1px;
  height: 30px;
  text-decoration: none;

  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;

  -moz-box-shadow: 0 1px 0 rgba(0,0,0,0.2);
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);

  cursor: pointer;

  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.stripe-connect span {
  display: block;
  position: relative;
  padding: 0 12px 0 44px;
  height: 30px;

  background: #1275FF;
  background-image: -webkit-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
  background-image: -moz-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
  background-image: -ms-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
  background-image: linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);

  font-size: 14px;
  line-height: 30px;
  color: white;
  font-weight: bold;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2);

  -moz-box-shadow: inset 0 1px 0 rgba(255,255,255,0.25);
  -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);

  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}

.stripe-connect span:before {
  content: '';
  display: block;
  position: absolute;
  left: 11px;
  top: 50%;
  width: 23px;
  height: 24px;
  margin-top: -12px;
  background-repeat: no-repeat;
  background-size: 23px 24px;
}

.stripe-connect:active {
  background: #005D93;
}

.stripe-connect:active span {
  color: #EEE;

  background: #008CDD;
  background-image: -webkit-linear-gradient(#008CDD, #008CDD 85%, #239ADF);
  background-image: -moz-linear-gradient(#008CDD, #008CDD 85%, #239ADF);
  background-image: -ms-linear-gradient(#008CDD, #008CDD 85%, #239ADF);
  background-image: linear-gradient(#008CDD, #008CDD 85%, #239ADF);

  -moz-box-shadow: inset 0 1px 0 rgba(0,0,0,0.1);
  -webkit-box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.1);
}

.stripe-connect:active span:before {

}

.stripe-connect.light-blue {
  background: #b5c3d8;
  background-image: -webkit-linear-gradient(#b5c3d8, #9cabc2);
  background-image: -moz-linear-gradient(#b5c3d8, #9cabc2);
  background-image: -ms-linear-gradient(#b5c3d8, #9cabc2);
  background-image: linear-gradient(#b5c3d8, #9cabc2);
  
  -moz-box-shadow: 0 1px 0 rgba(0,0,0,0.1);
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
}

.stripe-connect.light-blue span {
  color: #556F88;
  text-shadow: 0 1px rgba(255, 255, 255, 0.8);

  background: #f0f5fa;
  background-image: -webkit-linear-gradient(#f0f5fa, #e4ecf5 85%, #e7eef6);
  background-image: -moz-linear-gradient(#f0f5fa, #e4ecf5 85%, #e7eef6);
  background-image: -ms-linear-gradient(#f0f5fa, #e4ecf5 85%, #e7eef6);
  background-image: linear-gradient(#f0f5fa, #e4ecf5 85%, #e7eef6);
    
  -moz-box-shadow: inset 0 1px 0 #fff;
  -webkit-box-shadow: inset 0 1px 0 #fff;
  box-shadow: inset 0 1px 0 #fff;
}

.stripe-connect.light-blue:active {
    background: #9babc2;
}

.stripe-connect.light-blue:active span {
  color: #556F88;
  text-shadow: 0 1px rgba(255, 255, 255, 0.8);

  background: #d7dee8;
  background-image: -webkit-linear-gradient(#d7dee8, #e7eef6);
  background-image: -moz-linear-gradient(#d7dee8, #e7eef6);
  background-image: -ms-linear-gradient(#d7dee8, #e7eef6);
  background-image: linear-gradient(#d7dee8, #e7eef6);
    
  -moz-box-shadow: inset 0 1px 0 rgba(0,0,0,0.05);
  -webkit-box-shadow: inset 0 1px 0 rgba(0,0,0,0.05);
  box-shadow: inset 0 1px 0 rgba(0,0,0,0.05);
}

.stripe-connect.dark {
  background: #252525;
  background: rgba(0,0,0,0.5) !important;
}

/* Images*/

.stripe-connect span:before, .stripe-connect.blue span:before {
  background-image: url("data:image/png;base64,");
}

.stripe-connect.light-blue span:before {
  background-image: url("data:image/png;base64,");
}

/* Retina support */
@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
       only screen and (min--moz-device-pixel-ratio: 1.5),
       only screen and (min-device-pixel-ratio: 1.5) {

  .stripe-connect span:before, .stripe-connect.blue span:before {
    background-image: url("data:image/png;base64,");
  }

  .stripe-connect.light-blue span:before {
    background-image: url("data:image/png;base64,");
  }
}
</style>



